import {
  EnumAction,
  IExtractParam,
  IGrindParam,
  IPourOverParam,
  IRestParam,
} from "@/gateway/step";
import useDeviceContext from "@/hooks/useDeviceContext";
import { produce } from "immer";
import { MdOutlineClear } from "react-icons/md";
import { ActionRune } from "./components/ActionRune";
import { GrinderSkipRune } from "./components/GrinderRune";
import { MagicLine, RenderedSegment } from "./components/ProfileRune";
import {motion, AnimatePresence} from 'framer-motion'

import { useTranslation } from "next-export-i18n";
import { IngredientRune } from "./components/IngredientRune";
import { ProfileRune } from "./components/ProfileRune";
import { PumpRune } from "./components/PumpRune";
import { TextRune } from "./components/TextRune";
import { TimeRune } from "./components/TimeRune";
import { WeightRune } from "./components/WeightRune";

export type ProfilingType = "pressure sensor" | "flowrate profiler";
export type GrinderBehaviour = "can skip" | "must wait";
export type ActionType = "Grind" | "Extract" | "Rest" | "Profiling";

// ----------------------------------------------------------- //

function Wrapper() {
  return (
    <div className="group relative mx-auto max-w-7xl">
      <div className="absolute -inset-1 rounded-lg bg-gradient-to-r from-purple-600 to-pink-600 opacity-25 blur transition duration-1000 group-hover:opacity-100 group-hover:duration-200">
        <div className="items-pop flex justify-start space-x-6 rounded-lg bg-white px-7 py-6 leading-none ring-1 ring-gray-900"></div>
      </div>
    </div>
  );
}

export function NewStepCard({
  onDelete,
  index,
}: {
  onDelete: () => void;
  index: number;
}) {
  const { formulas, setFormulas, selectedFormulaId } = useDeviceContext();
  const steps = formulas[selectedFormulaId].steps || [];
  const { t } = useTranslation();
  return (
    <AnimatePresence>
      <motion.div
        key={index}
        initial={{
          scale: 0,
          opacity: 0
        }}
        animate={{
          scale: 1,
          opacity: 1
        }}
        transition={{
          ease: "easeInOut"
        }}
        exit={{
          scale: 0,
          opacity: 0
        }}


        className="-inset-1 rounded-xl bg-gradient-to-r from-purple-600 to-pink-600"
      >
        <div className="flex h-fit w-96 max-w-[calc(90vw)] flex-col rounded-xl  border  bg-white p-4 drop-shadow-xl transition-transform duration-200 hover:border-blue-500">
          <div
            className="absolute right-0 top-0 cursor-pointer p-2"
            onClick={() => onDelete()}
          >
            <MdOutlineClear className="text-gray-300" />
          </div>
          <div className="inline-flex max-w-96 flex-wrap items-center gap-2 space-x-1 text-wrap">
            <ActionRune index={index} />

            {formulas[selectedFormulaId].steps[index].action.actionType ===
              EnumAction.Grind && (
                <>
                  <WeightRune index={index} subIndex={-1} />
                  <TextRune text={t("of")} />
                  <IngredientRune
                    value={(steps[index].action as IGrindParam).ingredient}
                    onChange={(ingredient: string) => {
                      // setSteps(
                      //   produce(steps, (draft) => {
                      //     (draft[index].action as IGrindParam).ingredient =
                      //       ingredient;
                      //   })
                      // );

                      setFormulas(
                        produce(formulas, (draft) => {
                          (
                            draft[selectedFormulaId].steps[index]
                              .action as IGrindParam
                          ).ingredient = ingredient;
                        }),
                      );
                    }}
                  />
                  <TextRune text={t("bean, grinder")} />
                  <GrinderSkipRune
                    value={(steps[index].action as IGrindParam).canSkip}
                    onChange={(state: boolean) => {
                      // setSteps(
                      //   produce(steps, (draft) => {
                      //     (draft[index].action as IGrindParam).canSkip = state;
                      //     draft[index].action.actionType = EnumAction.Grind;
                      //   })
                      // );
                      setFormulas(
                        produce(formulas, (draft) => {
                          (
                            draft[selectedFormulaId].steps[index]
                              .action as IGrindParam
                          ).canSkip = state;
                          draft[selectedFormulaId].steps[index].action.actionType =
                            EnumAction.Grind;
                        }),
                      );
                    }}
                  />

                  {(steps[index].action as IGrindParam).canSkip === true ? (
                    <TextRune text={t("this step is portafilter is absent")} />
                  ) : (
                    <TextRune text={t("until portafilter is present")} />
                  )}
                </>
              )}
            {steps[index].action.actionType === EnumAction.Extract && (
              <>
                {/* <TextRune text="a shot of" /> */}
                <WeightRune index={index} subIndex={-1} />
                <TextRune text={t("with pump power")} />
                <PumpRune
                  value={(steps[index].action as IExtractParam).power}
                  onChange={(power: number) => {
                    // setSteps(
                    //   produce(steps, (draft) => {
                    //     (draft[index].action as IExtractParam).actionType =
                    //       EnumAction.Extract;
                    //     (draft[index].action as IExtractParam).power = power;
                    //   })
                    // );

                    setFormulas(
                      produce(formulas, (draft) => {
                        (
                          draft[selectedFormulaId].steps[index]
                            .action as IExtractParam
                        ).actionType = EnumAction.Extract;
                        (
                          draft[selectedFormulaId].steps[index]
                            .action as IExtractParam
                        ).power = power;
                      }),
                    );
                  }}
                />
              </>
            )}
            {steps[index].action.actionType === EnumAction.PourOver && (
              <>
                <TextRune text={t("total of")} />
                <WeightRune index={index} subIndex={-1} />
                <TextRune text={t(", extract")} />
                <WeightRune index={index} subIndex={0} />
                <TextRune text={t("every")} />
                <TimeRune subIndex={-2} index={index} />
                <TextRune text={t("with pump power of")} />
                <PumpRune
                  value={(steps[index].action as IPourOverParam).pumpPower}
                  onChange={(power: number) => {
                    // setSteps(
                    //   produce(steps, (draft) => {
                    //     (draft[index].action as IExtractParam).actionType =
                    //       EnumAction.Extract;
                    //     (draft[index].action as IExtractParam).power = power;
                    //   })
                    // );

                    setFormulas(
                      produce(formulas, (draft) => {
                        (
                          draft[selectedFormulaId].steps[index]
                            .action as IPourOverParam
                        ).actionType = EnumAction.PourOver;
                        (
                          draft[selectedFormulaId].steps[index]
                            .action as IPourOverParam
                        ).pumpPower = power;
                      }),
                    );
                  }}
                />

              </>
            )}
            {steps[index].action.actionType === EnumAction.Rest && (
              <>
                <TextRune text={t("for")} />
                <TimeRune
                  subIndex={-1}
                  index={index}
                // valveState={(steps[index].action as IRestParam).valveState}
                // toggleChecked={(checked: boolean) => {
                //   setSteps(
                //     produce(steps, (draft) => {
                //       draft[index].target.targetType = EnumTarget.Time;
                //       (draft[index].action as IRestParam).valveState = checked;
                //     })
                //   );
                // }}
                />
                {(steps[index].action as IRestParam).valveState && (
                  <TextRune text="but keep valve open" />
                )}
              </>
            )}
            {steps[index].action.actionType === "Profile" && (
              <>
                <TextRune text={t("to extract")} />

                <WeightRune index={index} subIndex={-1} />
                <TextRune text={t("using")} />
                <ProfileRune index={index} />
                <TextRune text={t("profiling")} />
                <RenderedSegment
                  index={index}
                // segments={(steps[index].action as IProfileParam).segments}
                // setSegments={() => {
                //   setSteps(
                //     produce(steps, draft => {
                //       (draft[index].action as IProfileParam).segments = segments;
                //     })
                //   );
                // }}
                // sensor={(steps[index].action as IProfileParam).profilingType}
                />
                <MagicLine
                  index={index}
                // segments={(steps[index].action as IProfileParam).segments}
                // sensor={(steps[index].action as IProfileParam).profilingType}
                />
              </>
            )}
          </div>
        </div>
      </motion.div>

    </AnimatePresence>
  );
}
